@font-face {
  font-family: 'Fira-Sans-Bold';
  font-style: normal;
  font-weight: bold;
  src: url(../fonts/Fira-Sans/FiraSans-Bold.woff) format("woff"); }

@font-face {
  font-family: 'Fira-Sans-Italic';
  font-style: italic;
  font-weight: normal;
  src: url(../fonts/Fira-Sans/FiraSans-Italic.woff) format("woff"); }

@font-face {
  font-family: 'Fira-Sans-Medium';
  font-style: normal;
  font-weight: 500;
  src: url(../fonts/Fira-Sans/FiraSansMedium.woff) format("woff"); }

@font-face {
  font-family: 'Fira-Sans-Regular';
  font-style: normal;
  font-weight: normal;
  src: url(../fonts/Fira-Sans/FiraSans.woff) format("woff"); }

.content {
  margin-left: auto;
  margin-right: auto;
  max-width: 1170px; }

.header {
  background: linear-gradient(0deg, rgba(255, 255, 255, 0.8) 0%, rgba(255, 255, 255, 0.8) 100%), url(../images/machine.png);
  background-size: 100% auto;
  font-family: 'Fira-Sans-Regular', sans-serif;
  height: 200px;
  width: 100%; }
  .header .content {
    padding-left: 15px;
    padding-top: 10px; }
  .header__top-bar {
    align-items: center;
    display: flex;
    justify-content: space-between;
    line-height: 150%; }
  .header__parameters-contacts {
    border-bottom: solid 1px #ccc;
    color: #999;
    display: flex;
    height: 70px;
    justify-content: space-between;
    padding-right: 15px;
    width: 1000px; }
  .header__parameters {
    display: flex; }
  .header__contacts {
    display: flex;
    margin-left: auto; }
  .header__contact-button {
    background: #0c4da2;
    border: none;
    border-radius: 6px;
    color: #fff;
    font-family: 'Fira-Sans-Bold', sans-serif;
    font-size: 14px;
    height: 46px;
    margin-left: 30px;
    padding: 15px 20px;
    width: 152px; }
  .header__bottom-bar {
    align-items: center;
    box-sizing: border-box;
    color: #333;
    display: flex;
    font-family: 'Fira-Sans-Bold', sans-serif;
    justify-content: space-between;
    padding-right: 15px;
    width: 100%; }
  .header__production {
    align-items: center;
    display: flex;
    justify-content: space-between;
    width: 70%; }
  .header__user-bar {
    align-items: center;
    display: flex; }

.logo__title_hidden {
  display: none; }

.logo__image {
  height: 92px; }

.parameters__lang-switcher {
  display: flex;
  list-style: none;
  margin-bottom: 0;
  margin-right: 50px;
  margin-top: 0;
  padding-left: 0; }

.lang-switcher__item {
  position: relative; }
  .lang-switcher__item:not(:last-child) {
    padding-right: 8px; }
    .lang-switcher__item:not(:last-child)::after {
      background: #999;
      content: '';
      height: 14px;
      position: absolute;
      right: 0;
      top: 4px;
      width: 1px; }
  .lang-switcher__item:not(:first-child) {
    padding-left: 8px; }

.lang-switcher__link {
  color: inherit;
  text-decoration: none; }
  .lang-switcher__link_current {
    color: #333;
    font-family: 'Fira-Sans-Bold', sans-serif; }

.currency__select {
  appearance: none;
  background: inherit;
  border: none;
  text-decoration: underline; }
  .currency__select:hover {
    cursor: pointer; }

.contacts__rus-phone {
  margin-left: 28px; }

.contacts__phone-number {
  color: #333;
  font-family: 'Fira-Sans-Bold', sans-serif;
  font-size: 20px;
  margin-left: 10px; }

.contacts__free-call {
  font-size: 14px;
  margin-left: auto;
  width: fit-content; }

.production__title {
  font-size: 32px; }

.production__type {
  display: flex;
  list-style: none;
  margin-bottom: 0;
  margin-top: 0;
  padding-left: 0; }

.production__item {
  position: relative; }
  .production__item:not(:last-child) {
    margin-right: 15px; }
    .production__item:not(:last-child)::after {
      background: #ccc;
      content: '';
      height: 20px;
      position: absolute;
      right: -15px;
      top: -2px;
      width: 1px; }
  .production__item:not(:first-child) {
    margin-left: 15px; }

.production__equipment {
  color: #999; }

.user-bar__title {
  margin-right: 30px; }

.user-bar__user-file {
  align-items: center;
  color: #0c4da2;
  display: flex;
  text-decoration: none; }

.user-file__svg {
  font-size: 18px;
  margin-right: 10px; }

.user-file__saved-amount {
  background: #0c4da2;
  border-radius: 50%;
  color: #fff;
  font-family: 'Fira-Sans-Regular', sans-serif;
  font-size: 14px;
  height: 22px;
  line-height: 22px;
  margin-left: 8px;
  text-align: center;
  width: 22px; }
